/* General styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #ffffff;
  width: 100%;
}

.logo {
  width: 120px; /* Adjust logo size */
  height: auto;
}

.nav-links {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 20px; /* Space between links */
}

.nav-links li {
  font-size: 16px !important; /* Default font size */
  cursor: pointer;
}

.join-button {
  padding: 8px 16px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.join-button:hover {
  background-color: #0056b3;
}

/* Responsive styles for medium mobile screens (max-width: 768px) */
@media (max-width: 768px) {
  .navbar{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .logo {
    width: 80px; /* Smaller logo for mobile */
  }

  .nav-links {
    display: none; /* Hide nav links on mobile screens */
  }

  .join-button {
    display: none; /* Hide join button on mobile screens */
  }
}

/* Styles for smaller mobile screens (max-width: 480px) */
@media (max-width: 480px) {
  .navbar{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px; /* Even smaller logo for very small screens */
  }

  .nav-links {
    display: none !important; /* Ensure nav links are hidden */
  }

  .join-button {
    display: none !important; /* Ensure the join button is hidden */
  }
}
